.login input,
.login select {
    border-bottom: 2px solid #25a18e;
    /* box-shadow: 4px 4px 12px 0px rgba(0, 0, 0, 0.08); */
    border-radius: 0px;
    letter-spacing: 0.01rem;
    box-shadow: none;
    font-size: 14px;
    border-top: 0;
    border-left: 0;
    border-right: 0;
}

.login input::placeholder{
    font-size: 13px;
}

.login select {
    background-color: #fff;
    border-radius: 0px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    font-size: 13px;
}

.login input:focus,
.login select:focus {
    outline: none !important;
}

.login select:focus {
    outline: none !important;
}

.login-chk input[type='checkbox'] {
    flex-shrink: 0;
    margin-top: 6px;
}